.PageWrapper{display:grid;place-items:center;margin-top:10px}.Table{background:rgba(0,0,0,0);border:1px solid rgba(73,80,87,.42);padding:10px;text-align:center;border-spacing:0;border-radius:10px;width:50%}@media screen and (max-width: 767px){.Table{width:90%}}.Table .head{font-weight:bolder}.Table .head th{padding:10px}.Table tbody tr:hover{background:hsla(210,8.75%,34.3725490196%,.12)}.Table tbody td{padding:10px;border-top:1px solid rgba(73,80,87,.42)}